import React, { Component } from 'react';
import axios from 'axios';
import {Consumer} from '../../Context';
import styled from 'styled-components';

const Wrapt = styled.div`
    margin-top: 30px;
`;

class Search extends Component {
state = {
    trackTitle:'',
}
handleChange = (e)=>{
    this.setState({[e.target.name] : e.target.value});
}
handleSubmit = (dispatch,e)=> {
    e.preventDefault();

    axios
      .get(
        `https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.search?q_track=${
          this.state.trackTitle
        }&page_size=10&page=1&s_track_rating=desc&apikey=f17c617b738bb52ec1e803a187404dd9`
      )
      .then(res => {
        dispatch({
          type: 'SEARCH_TRACKS',
          payload: res.data.message.body.track_list
        });

        this.setState({ trackTitle: '' });
      })
      .catch(err => console.log(err));
}
render() {
    return (
      <Consumer>
        {value=> {
            const { dispatch } = value;
            return(
                <Wrapt className="card card-body mb-4 p-4">
                    <h1 className="display-4 text-center">
                        <i className="fa fa-music"></i> Search for a song
                    </h1>
                        <p className="lead text-center">Get a lyrics for any song</p>   
                    <form onSubmit={this.handleSubmit.bind(this, dispatch)}>
                        <div className="form-group">
                            <input type="text" className="form-control form-control-lg" placeholder="Song title..." name="trackTitle"
                            value={this.state.trackTitle}
                            onChange={this.handleChange}
                            />
                        </div>
                        <button className="btn btn-primary btn-lg btn-block mb-5" type="submit" style={{background:"#2f496e", color:"#f4eade"}}>
                            Get  Track Lyrics
                        </button>
                    </form>
                </Wrapt>
            )
        }}

      </Consumer>
    )
  }
}

export default Search;
